body {
  font-family: 'Arial', sans-serif;
  margin: 0;
  padding: 0;
}

.header {
  background-color: #2D3A45;
  color: white;
  padding: 20px;
  text-align: center;
  position: sticky;
  top: 0;
  z-index: 10;
}

.header nav ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

.header nav ul li {
  display: inline;
  margin: 0 15px;
}

.header nav ul li a {
  color: white;
  text-decoration: none;
}

.hero {
  display: flex;
  justify-content: center;
  align-items: center;
  background: linear-gradient(135deg, #1C77C3, #39A0ED);
  color: white;
  padding: 100px 20px;
  text-align: center;
  position: relative;
}

.hero img {
  width: 100%;
  max-height: 400px;
  object-fit: cover;
  position: absolute;
  top: 0;
  left: 0;
  z-index: -1;
  opacity: 0.5;
}

.hero-content {
  z-index: 1;
}

.hero h2 {
  font-size: 48px;
  margin: 0;
}

.hero p {
  font-size: 24px;
  margin: 20px 0;
}

.cta-button {
  background-color: #FFD700;
  color: #2D3A45;
  padding: 15px 30px;
  border: none;
  border-radius: 5px;
  font-size: 18px;
  cursor: pointer;
}

.cta-button:hover {
  background-color: #FFC300;
}

.overview, .features, .cta {
  padding: 50px 20px;
  text-align: center;
}

.overview h2, .features h2, .cta h2 {
  font-size: 36px;
}

.overview p, .features ul {
  font-size: 18px;
}

.features-grid {
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
}

.feature-item {
  flex-basis: 20%;
  margin: 20px;
}

.feature-item img {
  width: 80px;
  height: 80px;
}

.footer {
  background-color: #2D3A45;
  color: white;
  text-align: center;
  padding: 20px;
  margin-top: 50px;
}
